
<style lang="less" scoped>
/* HEADER 返回按钮 START */
.header {
  height: 100px;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 33;
  border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  div {
    height: 100px;
    vertical-align: middle;
    display: inline-block;
    &:nth-child(1) {
      flex: 1;
      position: relative;
      & > img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 30px;
        height: 45px;
      }
    }
    &:nth-child(2) {
      flex: 7;
      padding-top: 30px;
      & > p {
        display: inline-block;
        vertical-align: middle;
        & > img {
          width: 70px;
          height: 70px;
          border-radius: 50%;
        }
        &:nth-child(2) {
          padding-left: 20px;
          & > span {
            display: block;
            &:nth-child(1) {
              color: #292929;
              font-size: 30px;
            }
            &:nth-child(2) {
              color: #969696;
              font-size: 26px;
            }
          }
        }
      }
    }
    &:nth-child(3) {
      flex: 2;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      color: #3eb140;
    }
  }
} // 头部
.workDetail {
  background-color: #fff;
  padding-bottom: 80px;
}
.post_item {
  width: 30%;
  -webkit-box-flex: 3;
  flex: 3;
}
.post_item {
  padding-top: 1.25rem;
}
.post_item > a {
  color: rgb(1, 4, 167);
}
.post_item_footer {
  text-align: right;
}
.post_item_footer > span > img {
  width: 1.5625rem;
  height: 1.5625rem;
  vertical-align: middle;
  margin-right: 0.3125rem;
}
.post_item_footer > span > span {
  vertical-align: middle;
}
.post_item > span {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 6.25rem;
  color: #5581a8;
}
.praiser {
  display: inline-block;
  color: #5581a8;
  font-size: 25px;
}
.post_content {
  padding: 0;
  & > p {
    margin: 0 auto;
    width: 95%;
    padding: 30px 30px 0 30px;
    color: #292929;
    font-size: 30px;
    margin-top: 20px;
    margin-bottom: 15px;
    box-sizing: border-box;
  }
}
.post_ceontent_list {
  width: 95%;
  margin: 0 auto;
  padding: 0 30px 20px 30px;
  box-sizing: border-box;
  & > p {
    font-size: 30px;
  }
}
.post_item_footer {
  width: 95%;
  padding: 20px 30px;
  margin: 0 auto;
  span > img {
    width: 40px;
    height: auto;
  }
}
.work-item-bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  padding-left: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
  & > span.active::after {
    position: absolute;
    content: "";
    width: 30px;
    height: 6px;
    border-radius: 5px;
    background-color: #3eb140;
    bottom: -29px;
    left: 0;
  }
  & > span {
    font-size: 30px;
    color: #554f4f;
    position: relative;
    & > img {
      vertical-align: middle;
      margin-right: 5px;
    }
    &:nth-child(1) {
      flex: 6;
      & > span {
        display: inline-block;
        width: 140px;
        height: 44px;
        text-align: center;
        line-height: 44px;
        border-radius: 0 20px 20px 20px;
        font-size: 24px;
        &.blueClass {
          background-color: rgb(169, 228, 255);
          color: rgb(28, 121, 237);
        }
        &.orangeClass {
          color: #ff5100;
          background-color: #ecc7a4;
        }
      }
    }
    &:nth-child(2) {
      flex: 2;
      & > img {
        width: 36px;
        height: 34px;
      }
    }
    &:nth-child(3) {
      flex: 2;
      & > img {
        width: 28px;
        height: 34px;
      }
    }
  }
}
.parise-container {
  overflow-x: hidden;
  width: 100%;
  padding-bottom: 100px;
  & > div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    & > div {
      &:nth-child(1) {
        text-align: center;
        flex: 2;
        & > img {
          width: 70px;
          height: 70px;
          border-radius: 50%;
        }
      }
      &:nth-child(2) {
        flex: 8;
      }
    }
  }
}
.comment-container {
  overflow-x: hidden;
  width: 100%;
  & > div {
    display: flex;
    justify-content: space-around;
    // align-items: center;
    margin-top: 20px;
    padding-left: 30px;
    position: relative;
    box-sizing: border-box;
    &::after {
      position: absolute;
      bottom: 0;
      right: -20px;
      width: 84%;
      content: "";
      height: 2px;
      background-color: #ddd;
    }
    & > div {
      padding-left: 20px;
      &:nth-child(1) {
        width: 10%;
        & > img {
          width: 70px;
          height: 70px;
          border-radius: 50%;
        }
      }
      &:nth-child(2) {
        width: 70%;
        word-wrap: break-word;
        & > span {
          display: block;
          font-size: 30px;
          &:nth-child(2) {
            padding: 18px 30px 18px 0;
            vertical-align: middle;
            &.audioClass {
              & > span {
                display: inline-block;
                color: #3eb140;
                width: 122px;
                height: 44px;
                vertical-align: middle;
                margin-right: 20px;
                background-position: center center;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                position: relative;
                & > b {
                  font-weight: normal;
                  position: absolute;
                  right: -50px;
                  top: 50%;
                  transform: translateY(-50%);
                }
              }
            }
          }
        }
        & > p {
          margin: 0;
          padding: 0 0 20px 0;
          font-size: 26px;
          color: #969696;
          & > span:nth-child(2) {
            &:nth-child(1) {
              font-size: 26px;
              color: #969696;
            }
            & > b:nth-child(2) {
              margin-left: 10px;
              font-weight: normal;
              min-height: 20px;
              min-width: 20px;
              text-align: center;
              display: inline-block;
              color: #fefefe;
              font-size: 20px;
              background-color: #979797;
              border-radius: 20%;
              padding: 0 7px;
            }
          }
        }
      }
      &:nth-child(3) {
        font-size: 26px;
        width: 20%;
        color: #969696;
      }
    }
  }
}
.bottom-options {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100px;
  background-color: #fff;
  width: 1040px;
  z-index: 5;
  @media screen and (max-width: 600px) {
    &{
      width: 750px;
    }
  }
  & > div {
    flex: 1;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
    &:nth-child(1) {
      & > img {
        vertical-align: middle;
        margin-right: 25px;
        width: 32px;
        height: 38px;
      }
    }
    &:nth-child(2) {
      & > img {
        vertical-align: middle;
        margin-right: 26px;
        width: 40px;
        height: 36px;
      }
    }
  }
}
.bottom-comment-options {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 100px;
  background-color: #fff;
  width: 100%;
  z-index: 5;
  & > div {
    text-align: center;
    &:nth-child(1) {
      flex: 7;
      text-align: right;
      & > textarea {
        min-height: 50px;
        height: auto;
        border: none;
        border: 0;
        outline: none;
        border-left: 4px solid #3eb140;
        padding-left: 20px;
        width: 400px;
        resize: none;
        overflow-y: visible;
      }
    }
    &:nth-child(2) {
      flex: 1;
      & > img {
        width: 50px;
        height: 50px;
      }
    }
    &:nth-child(3) {
      flex: 2;
      color: #3eb140;
      font-size: 30px;
    }
  }
}
.post_ceontent_list {
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  & > span {
    display: inline-block;
    width: 85px;
    height: 36px;
    position: absolute;
    bottom: 36px;
    right: 85px;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 20px;
    text-align: center;
    color: #fff;
    line-height: 36px;
    font-size: 30px;
    letter-spacing: 5px;
    z-index: 2;
  }
}
.swiper-slide.work-range-detail {
  background-position: center center;
  background-size: cover;
}

.footer-container{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .3);
  z-index: 100;
}
.autoWidth{
  width: auto !important;
}

</style>
<style>
.emoji-convert > img {
  width: 50px;
  height: 50px;
  vertical-align: middle;
}
.wc-pagination {
  display: none;
}
.swiper-wrapper > div{
  height: 300px;
}
.as-picture {
  width: 100%;
  height: 100%;
}
</style>

<template>
  <div class="workDetail">
    <!-- 设置返回按钮 START-->
    <div class="header">
      <div @click="back"><img :src="back_img" alt="返回" class="back"></div>
      <div>
        <p><img @click="__enterUserInfoCenter(workDetail)" :src="workDetail && workDetail.avatar" alt="头像"></p>
        <p>
          <span>{{workDetail.nickName}}</span><span>{{workDetail.submit | dateDiffTimestamp}}</span>
        </p>
      </div>
      <div @click="enterWorkRequire">作业要求</div>
    </div>
    <section class="recommend_container">
      <!-- 登录弹出层 -->
      <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
        <login v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
      </transition>
      <!-- 登录弹出层 -->
      <div class="recommend" id="recommend">
        <div class="post_content">
          <p>{{workDetail.jobContent}}</p>
          <Photoswipe>
            <ul class="post_ceontent_list">
              <!-- 轮播图 START -->
              <div class="swiper-container" id="banner-container">
                <div class="swiper-wrapper">
                  <div 
                    class="swiper-slide work-range-detail" 
                    v-for="(src, index) in (workDetail.imgSmall ? workDetail.imgSmall.split(',') : [])"
                    :data-pswp-src="src"
                    :key="`bg-${index}`"
                    :style="getImageItemStyle(src)">
                  </div>
                </div>
              </div>
              <span v-if="workDetail.imgSmall && workDetail.imgSmall && workDetail.imgSmall.split(',').length !== 1">{{currentBannerIndex + 1}}/{{workDetail.imgSmall.split(',').length}}</span>
              <!-- 轮播图 END -->
            </ul>
          </Photoswipe>
          <!-- <viewer v-if="!device">
            <ul class="post_ceontent_list">
      
              <div class="swiper-container" id="banner-container">
                <div class="swiper-wrapper">
                  <div 
                    class="swiper-slide work-range-detail" 
                    v-for="(src, index) in workDetail.imgSmall.split(',')"
                    :key="index"
                    :style="getImageItemStyle(src)">
                    <img :src="src" alt="" class="as-picture">
                  </div>
                </div>
              </div>
              <span v-if="workDetail.imgSmall && workDetail.imgSmall && workDetail.imgSmall.split(',').length !== 1">{{currentBannerIndex + 1}}/{{workDetail.imgSmall.split(',').length}}</span>
            </ul>
          </viewer> -->
        </div>
        <!-- 评论 END -->
      </div>
    </section>
    <!-- 作业内容底部区域 -->
    <div class="work-item-bottom" ref="workContent">
      <span>
        <span class="blueClass" v-if="workDetail.categoryName === '设计学院'">#设计学院</span>
        <span class="orangeClass" v-if="workDetail.categoryName === '美术学院'">#美术学院</span>
        <span v-if="item.categoryName === null || item.categoryName === ''"></span>
      </span>
      <span @click="changeSlideStatus(0)" :class="{active: !activeObj.bottomListIndex}">
        评论<span>{{workDetail.commentNum}}</span>
      </span>
      <span @click="changeSlideStatus(1)" :class="{active: activeObj.bottomListIndex}">
        点赞
        {{workDetail.praiseNumber}}
      </span>
    </div>
    <!-- 评论列表 -->
    <div :style="{display: activeObj.bottomListIndex ? 'none' : 'block'}" class="comment-container">
      <div v-if="commentList && commentList.length > 0" v-for="(commentItem, index) in commentList" :key="index">
        <div><img :src="commentItem.avatar" alt="头像"></div>
        <div>
          <span :style="{color: commentItem.userRoleList.join('').indexOf('instructor') > -1 ? '#3eb140' : '#292929'}">{{commentItem.nickName}}</span>
          <span @click="playAudio(commentItem)" class="audioClass" v-if="commentItem.commentType === 'AUDIO'">
            <span :style="{'background-image': 'url('+ (!audioPlayStatus ? audioImg : audioPlayingImg) +')'}"><b>{{commentItem.audioLength}}"</b></span>
            <audio ref="audioOption" :src="commentItem.content"></audio>
          </span>
          <span class="emoji-convert" v-if="commentItem.commentType === 'TEXT'" v-html="convertEmotions(commentItem.content)"></span>
          <p>
            <span>{{commentItem.commentTime | dateDiffTimestamp}}</span>
            <span @click="enterReplayComment(commentItem)" v-if="commentItem.replyCount === 0 || !LOGIN_USER_OBJECT">回复</span>
            <span @click="enterReplayComment(commentItem)" v-if="commentItem.replyCount >= 1"><b>回复</b><b>{{commentItem.replyCount}}</b></span>
          </p>
        </div>
        <div @click="delJobCommentById(commentItem, index)" v-if="LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.userId === commentItem.userId">删除</div>
        <div v-if="LOGIN_USER_OBJECT && LOGIN_USER_OBJECT.user.userId !== commentItem.userId"></div>
      </div>
    </div>
    <!-- 点赞列表 -->
    <div :style="{display: !activeObj.bottomListIndex ? 'none' : 'block'}" class="parise-container">
      <div v-for="(pariseItem, index) in praiseList" :key="index" v-if="pariseItem.userId">
        <div><img :src="pariseItem.avatar" alt="头像"></div>
        <div>{{pariseItem.nickName}}</div>
      </div>
    </div>
    <!-- 底部固定区域 -->
    
      <div class="bottom-options" @touchmove.prevent>
        <div @click="changeOptionStatus"><img src="../../assets/img/zuoyexiangqing_pinglun.png" alt="评论">评论</div>
        <div @click="pariseWork(workDetail)"><img :src="workDetail.isPraise === 'N' ? circleFollow : allFollow" alt="点赞">点赞</div>
      </div>
    <!-- 放大预览效果 -->
    <previewer v-if="showPreViewer" :previewerArr="previewerArr" @closePreview="closePreview"></previewer>
    <!-- 评论发布 -->
    <comment-modal class="animated" :workDetail="workDetail" v-if="showFooterBarStatus" @changeOptionStatus="changeOptionStatus"></comment-modal>
  </div>
</template>
<script>
import login from "@/components/login"
import { createNamespacedHelpers } from "vuex"
const { mapMutations, mapActions, mapState } = createNamespacedHelpers(
  "userModule"
)
import commentModal from '@/components/commentModal'
import previewer from '@/components/previewer'
import { device, isIOS } from "../../utils/device"
import { MessageBox } from 'mint-ui'
export default {
  components: {
    login,
    previewer,
    commentModal
  },
  data() {
    return {
      Arr:[1,2,3],
      placeholder: "写评论...",
      workDetail: {},
      item: {
        user: {},
        jobcompletionE: {}
      },
      // 未点赞
      circleFollow: require("../../assets/img/zuoyexiangqing_dianzan.png"),
      // 点赞
      allFollow: require("../../assets/img/shouye_dainzan_select.png"),
      smileEmoji: require("../../assets/img/smile.png"),
      // 语音播放
      audioImg: require("../../assets/img/yuwoxiangguan_yuyin.png"),
      audioPlayingImg: require("../../assets/img/sound.gif"),
      audioPlayStatus: 0,
      showLoginModal: 0,
      back_img: require('../../assets/img/per_fh.png'),
      // 当前轮播Index
      commentObj: {
        page: 1,
        pageSize: 5,
        falgNoData: false,
        pagination: true,
        showData: false
      },
      commentList: [],
      // 显示底部栏
      showFooterBarStatus: 0,
      // 评论内容
      commentInputValue: "",
      // 点赞操作需要的对象
      pariseObj: {
        page: 1,
        pageSize: 15,
        falgNoData: false,
        pagination: true,
        showData: false
      },
      // 点赞列表
      praiseList: [],
      activeObj: {
        bannerIndex: 0,
        bottomListIndex: 0,
        bannerSwiperObj: null,
        bottomListSwiperObj: null
      },
      currentBannerIndex: 0,
      // 显示预览轮播图
      showPreViewer: 0,
      previewerArr: [],
      previewerObj: null,
      audioLoop: null,
      jobcompletionId: '',
      // 页面加载时, 跳转到指定内容以及位置
      showContentStatus: 'comment'
    }
  },
  computed: {
    ...mapState(["LOGIN_USER_OBJECT"]),
    device() {
      return device;
    },
    isIOS(){
      return isIOS
    }
  },
  watch: {
    LOGIN_USER_OBJECT(state) {
      if (state) {
        Promise.all([
          this.getJobDetails(),
          this.getWorkCommentList(this.$route.params.jobcompletionId),
          this.getPariseList(this.$route.params.jobcompletionId)
        ]).then(data => {
          this.$Indicator.close()
        })
      }
    },
    praiseList(data) {
      return data
    },
    showFooterBarStatus(data){
      if(data){
        document.getElementsByTagName('body')[0].style.overflowY = 'hidden'
      }else{
        document.getElementsByTagName('body')[0].style.overflowY = 'auto'
      }
    },
    showEmoji(data){
      if(data){
        this.smileEmoji = require("../../assets/img/huifu_biaoqing.png")
      }else{
        this.smileEmoji = require("../../assets/img/smile.png")
      }
    }
  },
  destroyed() {
    window.onscroll = () => {
      return
    }
    clearInterval(this.audioLoop)
  },
  methods: {
    getImageItemStyle(src) {
      return {
          backgroundImage: `url(${src})`,
          backgroundPosition: 'center',
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
      }
    },
    //调用store中的action
    ...mapActions([
      'PINGLUN_DEL',
      'GET_COMMENT_LIST',
      'GET_JOB_DETAILS'
    ]),
    __enterUserInfoCenter(item) {
      this.$router.push({
        name: "perWorkCircle",
        params: {
          id: item.userId
        }
      })
    },
    closePreview(){
      this.showPreViewer = 0
    },
    
    // 打开预览
    openPreviewer(item, index) {

      let tag = item[index]
      item.splice(index, 1)
      item.unshift(tag)
      this.previewerArr = item
      this.showPreViewer = 1

      document.getElementsByTagName('body')[0].style.overflowY = 'hidden'
      
    },
    showDialogEvent(info) {
      this.showLoginModal = 0
    },
    transitionEndEvent(currentSlide) {
      this.currentBannerIndex = currentSlide
    },
    enterReplayComment(info) {
      // 跳转至二级回复页面 
      this.$router.push(`/replayComment/${info.commentId}/${info.jobcompletionId}/?type=${this.$route.query.type}&index=${this.$route.query.index}&@=${info.nickName}`)
    },
    // 切换Tap状态
    changeSlideStatus(info) {
      if (info) {
        this.activeObj.bottomListIndex = 1
      } else {
        this.activeObj.bottomListIndex = 0
      }
    },
    // 删除
    delJobCommentById(item, ind) {
      MessageBox.confirm('确认删除?').then(action => {
        this.commentList.splice(ind, 1)
        this.$Toast({
          message: "删除成功",
          position: "middle",
          duration: 1000
        })
        this.PINGLUN_DEL({
          commentId: item.commentId
        }).then(data => {
          if (data.data.status === "success") {
            
            this.$set(
              this.workDetail,
              "commentNum",
              --this.workDetail.commentNum
            )

            let type = this.$route.query.type
            let index = this.$route.query.index
            let workRangeModule = this.$store.state.workRangeModule
            switch(type){
              case 'hot':
                let WORK_HEAT_LIST = workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST
                if(WORK_HEAT_LIST.length > 0){
                  WORK_HEAT_LIST[index].commentList.forEach((ele, index_) => {
                    ele.comment.commentId === item.commentId ? WORK_HEAT_LIST[index].commentList.splice(index_, 1) : ''
                  })
                  --WORK_HEAT_LIST[index].commentNum
                }
                break;
              case 'new':
                let WORK_COMM_LIST = workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST
                if(WORK_COMM_LIST.length > 0){
                  WORK_COMM_LIST[index].commentList.forEach((ele, index_) => {
                    ele.comment.commentId === item.commentId ? WORK_COMM_LIST[index].commentList.splice(index_, 1) : ''
                  })
                  --WORK_COMM_LIST[index].commentNum
                }
                break;
              case 'mine':
                let WORK_MINE_LIST = workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST
                if(WORK_MINE_LIST.length > 0){
                  WORK_MINE_LIST[index].commentList.forEach((ele, index_) => {
                    ele.comment.commentId === item.commentId ? WORK_MINE_LIST[index].commentList.splice(index_, 1) : ''
                  })
                  --WORK_MINE_LIST[index].commentNum
                }
                break;
            }

            let routers = this.$route.fullPath

            this.$store.commit('cleanAboutAndMine', routers)
            } else {
              this.$Toast({
                message: "操作异常, 请稍后重试",
                position: "middle",
                duration: 1000
              })
            }
        }).catch(err => {
          console.log(err)
        })
      }).catch(err =>{
        //console.log('取消删除')
      })
     
    },
    // 播放音频
    playAudio(info) {
      if (this.$refs.audioOption[0].paused) {
        this.audioPlayStatus = 1
        this.$refs.audioOption[0].play()
        this.audioLoop = setInterval(() => {
          if (this.$refs.audioOption[0].paused) {
            this.audioPlayStatus = 0
            clearInterval(this.audioLoop)
          }
        }, 1000)
      } else {
        this.audioPlayStatus = 0
        this.$refs.audioOption[0].pause()
      }
    },
    // 改变底部栏显示状态
    changeOptionStatus(data) {

      if (!this.LOGIN_USER_OBJECT) {
        this.showLoginModal = 1
        return
      }
      this.jobcompletionId = this.workDetail.jobcompletionId
      if(!this.showFooterBarStatus){
        this.showFooterBarStatus = true
      }
      if(typeof data === 'object'){
        if(data.userId){ 
          this.showFooterBarStatus = false
          this.commentList.unshift(data)

          ++this.workDetail.commentNum

          let type = this.$route.query.type
          let index = this.$route.query.index
          let workRangeModule = this.$store.state.workRangeModule
          switch(type){
            case 'hot':
              let WORK_HEAT_LIST = workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST
              if(WORK_HEAT_LIST.length > 0){
                WORK_HEAT_LIST[index].commentList.unshift({
                  comment: data,
                  parentComment: {}
                })
                ++WORK_HEAT_LIST[index].commentNum
              }
              break;
            case 'new':
              let WORK_COMM_LIST = workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST
              if(WORK_COMM_LIST.length > 0){
                WORK_COMM_LIST[index].commentList.unshift({
                  comment: data,
                  parentComment: {}
                })
                ++WORK_COMM_LIST[index].commentNum
              }
              break;
            case 'mine':
              let WORK_MINE_LIST = workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST
              if(WORK_MINE_LIST.length > 0){
                WORK_MINE_LIST[index].commentList.unshift({
                  comment: data,
                  parentComment: {}
                })
                ++WORK_MINE_LIST[index].commentNum
              }
              break;
          }
        }
      }else{
        this.showFooterBarStatus = false
      }
    },
    // 上拉加载
    dropDown() {
      if (!this.activeObj.bottomListIndex) {
        if (this.falgNoData) {
          //数据全部加载完了
          return
        } else if (
          this.commentObj.pagination
        ) {
          //如果满足触发条件，执行
          this.commentObj.pagination = false
          this.commentObj.page += 1
          
          this.loading()
          this.getWorkCommentList(this.workDetail.jobcompletionId)
            .then(data => {
              this.hideLoading()
              if (data.data.result.list && data.data.result.list.length > 0) {
                data.data.result.list.forEach(element => {
                  if (element.avatar && !element.avatar.startsWith("http")) {
                    element.avatar = this.productURL + element.avatar
                  }
                })
                this.commentList = this.commentList.concat(
                  data.data.result.list
                )
                this.commentObj.pagination = true
              } else {
                this.hideLoading()
                this.commentObj.pagination = false
                this.$Toast({
                  message: "没有更多了~",
                  position: "middle",
                  duration: 1000
                })
              }
            })
            .catch(err => {
              console.log(err)
            })
        }
      } else {
        if (this.falgNoData) {
          //数据全部加载完了
          return
        } else if (
          this.pariseObj.pagination
        ) {
          //如果满足触发条件，执行
          this.loading()
          this.pariseObj.page += 1

          this.getPariseList(this.workDetail.jobcompletionId)
            .then(data => {
              this.hideLoading()
              if (data.data.result.list && data.data.result.list.length > 0) {
                this.pariseObj.pagination = true
              } else {
                this.pariseObj.pagination = false
                this.$Toast({
                  message: "没有更多了~",
                  position: "middle",
                  duration: 1000
                })
              }
            })
            .catch(err => {
              console.log(err)
            })
        }
      }
    },
    // 进入作业要求
    enterWorkRequire() {
      this.$router.push({
        name: "workRe",
        params: {
          taskId: this.$route.params.taskId,
          title: "workRe"
        }
      })
    },
    // 显示emojiModal
    showEmojiModalEvent() {
      if (this.emojiStatus) {
        this.showEmoji = 1
        this.emojiStatus = false
      } else {
        this.showEmoji = 0
        this.emojiStatus = true
      }
    },
    back() {
      this.$router.go(-1)
    },
    // 获得评论列表
    getWorkCommentList(jobcompletionId) {
      return new Promise((resolve, reject) => {
        this.GET_COMMENT_LIST({
          jobcompletionId: jobcompletionId,
          page: this.commentObj.page,
          pageSize: this.commentObj.pageSize
        }).then(data => {
           data.data.result.list.forEach(element => {
              if (!element.avatar.startsWith("http")) {
                element.avatar = this.productURL + element.avatar
              }
            })
            if(JSON.stringify(data.data.result.list) !== JSON.stringify(this.commentList)){
              this.commentList = this.commentList.concat(
                data.data.result.list
              )
            }
            resolve(data)
        }).catch(err => {
          console.log(err)
        })
      })
    },
    // 获取作业的详细信息
    getJobDetails() {
      return new Promise((resolve, reject) => {
        this.GET_JOB_DETAILS({
          taskId: this.$route.params.taskId,
          userId: this.$route.params.userId,
          jobcompletionId: this.$route.params.jobcompletionId
        }).then(data => {
           if ( data.data.result.list && data.data.result.list[0] ) {
              let taskInfo = data.data.result.list[0]
              if (!taskInfo.avatar.startsWith("http")) {
                taskInfo.avatar =
                  this.productURL + taskInfo.avatar
              }
              taskInfo['doPariseStatus'] = true
              taskInfo['doCommentStatus'] = true
              this.workDetail = taskInfo
              resolve(taskInfo)
            }
            this.$Indicator.close()
        }).catch(err => {
          this.$router.push("/err")
          console.log(err)
        })
      })
    },
    // 点赞操作
    pariseWork(workDetail) {


      if (!this.LOGIN_USER_OBJECT) {
        this.showLoginModal = 1
        return
      }

      if(this.workDetail.doPariseStatus){

        this.workDetail.doPariseStatus = false

        this.axios.post("/api/jobsList/parise", {
          jobcompletionId: this.workDetail.jobcompletionId,
          action: workDetail.isPraise === "Y" ? "NORMAL" : "UPVOTE"
        }).then(data => {

          this.workDetail.doPariseStatus = true
          this.$set(workDetail, "isPraise", data.data.status)
          if (data.data.status === "Y") {
            this.$Toast({
              message: "点赞成功",
              position: "middle",
              duration: 1000
            })
            this.$set(
              workDetail,
              "praiseNumber",
              ++this.workDetail.praiseNumber
            )

            let type = this.$route.query.type
            let index = this.$route.query.index
            let workRangeModule = this.$store.state.workRangeModule
            switch(type){
              case 'hot':
                let WORK_HEAT_LIST = workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST
                if(WORK_HEAT_LIST.length > 0){
                  WORK_HEAT_LIST[index].isPraise = 'Y'
                  ++WORK_HEAT_LIST[index].praiseNumber
                }
                break;
              case 'new':
                let WORK_COMM_LIST = workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST
                if(WORK_COMM_LIST.length > 0){
                  WORK_COMM_LIST[index].isPraise = 'Y'
                  ++WORK_COMM_LIST[index].praiseNumber
                }
                break;
              case 'mine':
                let WORK_MINE_LIST = workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST
                if(WORK_MINE_LIST.length > 0){
                  WORK_MINE_LIST[index].isPraise = 'Y'
                  ++WORK_MINE_LIST[index].praiseNumber
                }
                break;
            }

            this.praiseList.unshift({
              avatar: this.LOGIN_USER_OBJECT.user.avatar,
              nickName: this.LOGIN_USER_OBJECT.user.nickname,
              userId: this.LOGIN_USER_OBJECT.user.userId
            })
          } else {
            this.$Toast({
              message: "取消点赞",
              position: "middle",
              duration: 1000
            })
            this.$set(
              workDetail,
              "praiseNumber",
              --this.workDetail.praiseNumber
            )

            let type = this.$route.query.type
            let index = this.$route.query.index
            let workRangeModule = this.$store.state.workRangeModule
            switch(type){
              case 'hot':
                let WORK_HEAT_LIST = workRangeModule.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST
                if(WORK_HEAT_LIST.length > 0){
                  WORK_HEAT_LIST[index].isPraise = 'N'
                  --WORK_HEAT_LIST[index].praiseNumber
                }
                break;
              case 'new':
                let WORK_COMM_LIST = workRangeModule.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST
                if(WORK_COMM_LIST.length > 0){
                  WORK_COMM_LIST[index].isPraise = 'N'
                  --WORK_COMM_LIST[index].praiseNumber
                }
                break;
              case 'mine':
                let WORK_MINE_LIST = workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST
                if(WORK_MINE_LIST.length > 0){
                  WORK_MINE_LIST[index].isPraise = 'N'
                  --WORK_MINE_LIST[index].praiseNumber
                }
                break;
            }

            this.praiseList.forEach((ele, index) => {
              if(ele.userId === this.LOGIN_USER_OBJECT.user.userId){
                this.praiseList.splice(index, 1)
                return
              }
            })
          }

          let routers = this.$route.fullPath
          
          this.$store.commit('cleanAboutAndMine', routers)
        })
        .catch(err => {
          console.log(err)
        })
      }
    },
    // 获取点赞列表
    getPariseList(jobcompletionId) {
      return new Promise((resolve, reject) => {
        this.axios
          .get("/api/getJobPraiseList", {
            params: {
              page: this.pariseObj.page,
              pageSize: this.pariseObj.pageSize,
              jobcompletionId
            }
          })
          .then(data => {
            if (data.data.result.list && data.data.result.list.length > 0) {
              data.data.result.list.forEach(element => {
                if (element.avatar && !element.avatar.startsWith("http")) {
                  element.avatar = this.productURL + element.avatar
                }
              })
              this.praiseList = this.praiseList.concat(data.data.result.list)
            }
            resolve(data)
          })
          .catch(err => {
            console.log(err)
          })
      })
    }
  },
  destroyed () {
    window.onscroll = () => {
      return
    }
  },
  mounted() {

    
    this.$nextTick(() => {
      if (this.device) {
        let _this = this;
        this.previewerObj = new Swiper("#previewer", {
          observer:true, // 修改swiper自己或子元素时，自动初始化swiper
          observeParents:true, // 修改swiper的父元素时，自动初始化swiper
          on: {
            slideChangeTransitionEnd: function(){
              // _this.mobileImgIndex = this.activeIndex
            }
          }
        })
      } else {
        this.viewImg = this.previewerArr[this.imgIndex]
      }
    })
    this.$route.query.type === 'praise' ? this.activeObj.bottomListIndex = 1 : this.activeObj.bottomListIndex = 0

    this.$nextTick(() => {
      let _this = this
      this.bannerSwiperObj = new Swiper("#banner-container", {
        passiveListeners: false,
        observer: true, //修改swiper自己或子元素时，自动初始化swiper    
        observeParents: true, //修改swiper的父元素时，自动初始化swiper  
        on: {
          slideChangeTransitionEnd() {
            _this.currentBannerIndex = this.activeIndex
          }
        }
      })
    })

    this.$Indicator.open({
      text: "加载中...",
      spinnerType: "fading-circle"
    })

    Promise.all([
      this.getJobDetails(),
      this.getWorkCommentList(this.$route.params.jobcompletionId),
      this.getPariseList(this.$route.params.jobcompletionId)
    ]).then(data => {
      this.$Indicator.close()
      window.onscroll = () => {
        if(this.scrollEvent()){
          this.dropDown()
        }
      }

      // 若为点赞进入的话, 则进行指定位置滚动
      if(this.$route.query.type === 'praise' || this.$route.query.type === 'comment'){
        window.scrollTo(0, this.offsetTop(this.$refs.workContent).top - 60)
      }

    })
  }
}
</script>


