<style lang="less" scoped>
// previewer
.preview-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.3);
  height: 100vh;
}
.swiper-slide.previewer {
  width: 100%;
  min-height: 100vh;
  & > div {
    width: 100vw;
    height: 100vh;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    & > img {
      width: 100vw;
    }
  }
}
.previewer-container-pc {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 40;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  & > div.prev {
    position: fixed;
    top: 50%;
    left: 5%;
    transform: translateY(-50%);
  }
  & > div.next {
    position: fixed;
    top: 50%;
    right: 5%;
    transform: translateY(-50%);
  }
}
div.img-container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 41;
    & >img{
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}
.download{
  position: fixed;
  z-index: 42;
  top: 5%;
  right: 5%;
  width: 100px;
  height: 40px;
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 30px;
  color: #fff;
  outline: none;
  cursor: pointer;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
}
.full-screen{
  position: fixed;
  z-index: 42;
  top: 5%;
  right: 13%;
  width: 100px;
  height: 40px;
  background-color: transparent;
  border: 1px solid #ccc;
  border-radius: 30px;
  color: #fff;
  outline: none;
  cursor: pointer;
  font-size: 20px;
}
.previewerArr{
  position: fixed;
  top: 5%;
  left: 5%;
  display: block;
  width: 100px;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #ccc;
  background-color: transparent;
  text-align: center;
  color: #fff;
  z-index: 41;
  line-height: 40px;
  font-size: 25px;
}
.mobile-style-img{
  -webkit-touch-callout:default;
}
.imgIndex{
  position: fixed;
  top: 50px;
  left: 50px;
  color: #fff;
  font-size: 30px;
  z-index: 100;
}
</style>

<template>
    <!--  放大预览 -->
    <transition-group name="fade" mode="out-in" tag="div">
        <!-- M   -->
      <div v-if="device" class="preview-container" key="preview">
          <div class="swiper-container" id="previewer">
              <span class="imgIndex">{{mobileImgIndex + 1}}/{{previewerArr.length}}</span>
              <div class="swiper-wrapper" >
                  <a class="download" key="download" v-if="!isIOS" :href="previewerArr[imgIndex]" :download="previewerArr[imgIndex]">下载</a>
                  <div class="swiper-slide previewer" v-for="(item, index) in previewerArr" :key="index">
                      <div>
                        <img  @click.stop.prevent="closePreview" :class="{ autoWidth: device ? 'mobile-style-img' : 'autoWidth' }" :src="item" :alt="item">
                      </div>
                  </div>
              </div>
          </div>
      </div>
      <!-- PC -->
      <div v-if="!device" class="previewer-container-pc" @click.stop="closePreview" key="previewer-container-pc">
            <div class="prev" @click.stop="prev"><img src="../assets/img/jiantou_zuo.png" alt="上一张"></div>
            <div class="next" @click.stop="next"><img src="../assets/img/jiantou_zuo2.png" alt="下一张"></div>
      </div>
      <div class="img-container" v-if="!device" key="img-container">
        <img :src="viewImg" :alt="viewImg" @mousedown.prevent.stop="getPosition" @mousemove="addPosition" @mouseup="removePosition" @mousewheel="whellOnImg($event)" ref="autoWidthObj">
      </div>
      <a class="download" key="download" :href="this.previewerArr[this.imgIndex]" :download="this.previewerArr[this.imgIndex]">下载</a>
      <button class="full-screen" key="full-screen" @click="requestFullScreen">全屏</button>
      <span class="previewerArr" key="previewerArr">{{imgIndex + 1}}/{{previewerArr.length}}</span>
    </transition-group>
</template>
<script>
import { device, isIOS } from "../utils/device"
export default {
  props: ["previewerArr"],
  data() {
    return {
        // 放大比例
      wheelData: 0,
    //   图片链接
      viewImg: "",
    //   当前图片索引
      imgIndex: 0,
    //   全局X
      diffX: 0,
    //   全局Y
      diffY: 0,
    //   初始X
      initX: 0,
    //   初始Y
      initY: 0,
    //   开关 => 是否执行拖动
      flag: false,
      // 是否全屏
      fullStatus: true,
      mobileImgIndex: 0
    };
  },
  computed: {
    device() {
      return device;
    },
    isIOS(){
      return isIOS
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (this.device) {
        let _this = this;
        this.previewerObj = new Swiper("#previewer", {
          observer:true, // 修改swiper自己或子元素时，自动初始化swiper
          observeParents:true, // 修改swiper的父元素时，自动初始化swiper
          on: {
            slideChangeTransitionEnd: function(){
              // _this.mobileImgIndex = this.activeIndex
            }
          }
        })
      } else {
        this.viewImg = this.previewerArr[this.imgIndex]
      }
    })

  },
  methods: {
    requestFullScreen(){
      if(this.fullStatus){
        this.fullScreen()
      }else{
        this.cancelFullScreen() 
      }
    },
    // 全屏操作
    fullScreen(){
      this.fullStatus = !this.fullStatus
      var de = document.documentElement;
      if (de.requestFullscreen) {
          de.requestFullscreen();
        } else if (de.mozRequestFullScreen) {
          de.mozRequestFullScreen();
        } else if (de.webkitRequestFullScreen) {
          de.webkitRequestFullScreen();
      }
    },
    // 退出全屏操作
    cancelFullScreen(){
      var de = document;
        if (de.exitFullscreen) {
            de.exitFullscreen();
        } else if (de.mozCancelFullScreen) {
            de.mozCancelFullScreen();
        } else if (de.webkitCancelFullScreen) {
            de.webkitCancelFullScreen();
        }
        this.fullStatus = !this.fullStatus
    },
    removePosition(e){
        this.flag = false
    },
    addPosition(e){
        let ev = e || event
        ev.preventDefault()
        if(this.flag){
            var mouseMoveX = e.pageX,mouseMoveY = e.pageY
            this.$refs.autoWidthObj.style.position = 'fixed'
            this.$refs.autoWidthObj.style.left = parseInt(mouseMoveX) - parseInt(this.diffX) + parseInt(this.initX) + "px"
            this.$refs.autoWidthObj.style.top = parseInt(mouseMoveY) - parseInt(this.diffY) + parseInt(this.initY) + "px"
        }
    },
    getPosition(e){
        let ev = e || event
        this.diffX = ev.pageX
        this.diffY = ev.pageY

        this.initX = this.$refs.autoWidthObj.offsetLeft
        this.initY = this.$refs.autoWidthObj.offsetTop
        this.flag = true
    },
    prev() {
      if (this.imgIndex === 0) {
        this.$Toast({
          message: "已经是第一张了",
          position: "middle",
          duration: 1000
        });
      } else {
        this.$refs.autoWidthObj.style.zoom = "1";
        this.viewImg = this.previewerArr[--this.imgIndex];
      }
    },
    next() {
      if (this.imgIndex === this.previewerArr.length - 1) {
        this.$Toast({
          message: "已经是最后一张了",
          position: "middle",
          duration: 1000
        });
      } else {
        this.$refs.autoWidthObj.style.zoom = "1";
        this.viewImg = this.previewerArr[++this.imgIndex];
      }
    },
    addEvtListener(obj, evtName, f) {
      if (typeof obj !== "object") return;
      /* FF */
      if (document.addEventListener) {
        if (obj) {
          obj.addEventListener(evtName, f, false);
        } else {
          addEventListener(evtName, f, false);
        }
      } else if (attachEvent) {
        /* IE */
        if (obj) {
          obj.attachEvent("on" + evtName, f);
        } else {
          attachEvent("on" + evtName, f);
        }
      }
    },
    whellOnImg($event) {
      let zoom = 0;
      zoom += Math.ceil($event.wheelDelta);

      this.wheelData += zoom / 3000;

      this.$refs.autoWidthObj.style.position = "fixed";
      this.$refs.autoWidthObj.style.top = "50%";
      this.$refs.autoWidthObj.style["transform-origin"] = "50% 50%";
      this.$refs.autoWidthObj.style["transform"] = "translate(-50%, -50%)";

      if (this.wheelData <= 1) {
        this.wheelData = 1;
        this.$refs.autoWidthObj.style.zoom = `1`;
        this.$refs.autoWidthObj.style.position = 'fixed'
        this.$refs.autoWidthObj.style["transform"] = "translate(0, 0) translate(-50%, -50%)"
        this.$refs.autoWidthObj.style.top = '50%'
        this.$refs.autoWidthObj.style.left = '50%'
      } else {
        this.$refs.autoWidthObj.style.zoom = `${this.wheelData}`
        this.$refs.autoWidthObj.style.top = ''
        this.$refs.autoWidthObj.style.left = ''
      }
    },
    // 关闭预览
    closePreview(e) {
      let ev = e || event
      this.showPreViewer = 0
      this.previewerObj = null
      document.getElementsByTagName("body")[0].style.overflowY = "auto"
      this.$emit("closePreview", 0)
      this.cancelFullScreen()
    }
  }
};
</script>


